import React from 'react';
import { useRef, useEffect, useState } from 'react';
import {
  motion,
  useInView,
  useAnimation,
  AnimatePresence,
} from 'framer-motion';
import DarkBG from './DarkBG';

function Predavaci() {
  const [showModal, setShowModal] = useState(false);
  const ref = useRef(null);
  const isInView = useInView(ref);
  const cardControl = useAnimation();
  const headLineControl = useAnimation();

  showModal
    ? (document.body.style.overflow = 'hidden')
    : (document.body.style.overflow = 'unset');

  useEffect(() => {
    if (isInView) {
      cardControl.start('visible');
      headLineControl.start('visible');
    }
  }, [isInView]);
  return (
    <div className="predavaci bottom-margin" ref={ref} id="predavaci">
      <AnimatePresence>
        {showModal && (
          <DarkBG showModal={showModal} setShowModal={setShowModal} />
        )}
      </AnimatePresence>
      <motion.div
        className="section-name pd-top"
        variants={{
          hidden: { scale: 0, opacity: 0 },
          visible: { scale: 1, opacity: 1 },
        }}
        initial="hidden"
        animate={cardControl}
        transition={{
          duration: 1,
          delay: 0.5,
        }}
      >
        <h1>NAŠ TIM</h1>
      </motion.div>
      <div className="cards-wrap">
        <motion.div
          id="cardM"
          className="card"
          variants={{
            hidden: { y: 150, opacity: 0 },
            visible: { y: 0, opacity: 1 },
          }}
          initial="hidden"
          animate={cardControl}
          // whileHover={{ scale: 1.05 }}
          transition={{
            y: { duration: 1, delay: 1.2 },
            opacity: { duration: 1, delay: 1.2 },
            type: 'spring',
            stiffness: 400,
            damping: 10,
          }}
          // onClick={(e) => {
          //   setShowModal(true);
          //   console.log(e.currentTarget.id);
          // }}
        >
          <div className="profile-image"></div>
          <div className="profile-name">
            Mirjana Matić <br />
            Osnivac &amp; Direktor BFS Company
          </div>
          <div className="profile-text">
            <p>
              Svoje iskustvo sticala je u korporativnom svetu radeći dugi niz
              godina u nekoliko renomiranih kompanija, kao što su Mercator-S
              doo, Fructus doo, Tehnomanija doo, na rukovodećim pozicijama u
              okviru veleprodaje i prodaje, a posle finansija i finansijskog
              kontrolinga. Kao master ekonomista posebno se istakla kao ekspert
              u likvidnosti, kontroli finansijskih rezultata i kriznom
              menadžmentu.
            </p>
          </div>
          <div className="icons ">
            <a
              href="https://www.linkedin.com/in/mirjana-matic-99938774/"
              target="_blank"
            >
              <i className="fa-brands fa-linkedin fa-xl"></i>
            </a>
            <a href="https://www.instagram.com/mira_matic_/" target="_blank">
              <i className="fa-brands fa-instagram fa-xl"></i>
            </a>
          </div>
        </motion.div>
        <motion.div
          className="card"
          variants={{
            hidden: { y: 150, opacity: 0 },
            visible: { y: 0, opacity: 1 },
          }}
          initial="hidden"
          animate={cardControl}
          transition={{
            duration: 1,
            delay: 1.2,
          }}
        >
          <div className="profile-image-g"></div>
          <div className="profile-name">
            Marko Gilić <br />
            IT Konsultant
          </div>
          <div className="profile-text">
            <p>
              Znanja iz oblasti IT-a i web programiranja stekao je kako kroz
              formalno obrazovanje, tako i kroz samostalne projekte i
              kontinuirano usavršavanje. Takodje, veliko iskustvo poseduje iz
              oblasti logistike koje je stekao kroz rad u medjunarodnim
              kompanijama Havi Logistic i Stellantis. Poznavanje biznis procesa
              velikih organizacija u velikoj meri doprinelo je razvoju IT
              karijere kao konsultanta i web programera.
            </p>
          </div>
          <div className="icons">
            <a
              href="https://www.linkedin.com/in/marko-gilic-b9506810b/"
              target="_blank"
            >
              <i className="fa-brands fa-linkedin fa-xl"></i>
            </a>
            <a href="https://www.instagram.com/gilicm/" target="_blank">
              <i className="fa-brands fa-instagram fa-xl"></i>
            </a>
            <a href="https://github.com/markogilic" target="_blank">
              <i className="fa-brands fa-github fa-xl"></i>
            </a>
            {/* <i className="fa-solid fa-globe fa-xl"></i> */}
          </div>
        </motion.div>
        {/* <motion.div
          className="card remove"
          variants={{
            hidden: { y: 150, opacity: 0 },
            visible: { y: 0, opacity: 1 },
          }}
          initial="hidden"
          animate={cardControl}
          transition={{
            duration: 1,
            delay: 1.5,
          }}
        >
          <div className="profile-image-mkt"></div>
          <div className="profile-name">
            Bojana Rudović
            <br />
            Marketing Konsultant
          </div>
          <div className="profile-text">
            <p>
              Preko 20 godina dugu i uspešnu karijeru izgradila je u oblasti
              marketinga radeći na razvoju ključnih brendova našeg tržišta, kao
              i definisanju marketing strategija u okviru kompanija Beohemija,
              Banini, Nelt, Bekament i Logera. Danas, kao konsultant, pruža
              ekspertsku podršku u procesu identifikovanja poslovnih slabosti i
              izazova, kao i definisanju marketinga i G2M (go to market)
              strategija.
            </p>
          </div>
          <div className="icons">
            <a
              href="https://rs.linkedin.com/in/bojana-rudovic-5a37a425"
              target="_blank"
            >
              <i className="fa-brands fa-linkedin fa-xl"></i>
            </a>

            <a
              href="https://www.instagram.com/bojana_papic_rudovic/?igshid=MzRlODBiNWFlZA%3D%3D"
              target="_blank"
            >
              <i className="fa-brands fa-instagram fa-xl"></i>
            </a>
            {/* <i className="fa-solid fa-globe fa-xl"></i> 
          </div>
        </motion.div> */}
      </div>
    </div>
  );
}

export default Predavaci;
