import SvgAnimation from './component/SvgAnimation';

import { useState } from 'react';
import { AnimatePresence } from 'framer-motion';
import HeroSection from './component/HeroSection';
import Bfs from './component/Bfs';
import Predavaci from './component/Predavaci';
import Kursevi from './component/Kursevi';
import Blogs from './component/Blogs';
import Contact from './component/Contact';
import Footer from './component/Footer';
import CookieConsent from 'react-cookie-consent';

function App() {
  const [logoAnimation, setLogoAnimatio] = useState(true);
  const [webSite, setWebSite] = useState(false);

  setTimeout(() => {
    setLogoAnimatio(false);
    setWebSite(true);
  }, 4000);

  return (
    <div>
      <AnimatePresence>{logoAnimation && <SvgAnimation />}</AnimatePresence>
      {webSite && (
        <>
          <HeroSection />
          <Bfs />
          <Predavaci />
          <Kursevi />
          <Blogs />
          <Contact />
          <Footer />
          <CookieConsent
            location="bottom"
            buttonText="Pristajem"
            cookieName="aproveGA4"
            style={{
              background: '#485696',
              textAlign: 'center',
              fontFamily: 'Inter',
              fontSize: '16px',
            }}
            buttonStyle={{
              background: '#fc7a1e',
              borderRadius: '10px',
              color: '#fff',
              fontFamily: 'Inter',
              fontSize: '16px',
            }}
          >
            Koristimo kolačiće kako bismo vam pružili što bolje iskustvo na
            našem web sajtu i skupljamo podatke o korisnicima.{' '}
            <a
              style={{
                color: '#fff',
              }}
              href="politika_privatnosti.html"
              target="_blank"
            >
              Politika privatnosti
            </a>
          </CookieConsent>
        </>
      )}
    </div>
  );
}

export default App;
